import { Injectable,ChangeDetectorRef } from '@angular/core';
import { product } from './../models/product';

import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import * as Appconfig from "../services/common";
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/toPromise';

@Injectable()
export class productService {

  private dataUrl =  Appconfig.data.Url+'product'; //'http://localhost:3001/product';  // URL to web api
      testResponse: any;

  constructor(private http:Http) {}

get(): Observable<product[]> {
     return this.http.get(this.dataUrl+ '/list')
     .map((res:Response) => res.json());
         // .map(this.extractData)
         // .catch(this.handleError);
 }


save(obj:FormData):Observable<product[]> {
  console.log('SaveData - 2');

        let bodyString = JSON.stringify(obj); // Stringify payload
        console.log(bodyString);
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
     //this.http.post('http://localhost:8001/expensetype/Add', {'data':obj}, options).toPromise();
     return this.http.post(this.dataUrl+ '/Add',obj)
                    .map((res:Response) => res.json());
                   //  .then(this.extractData)
                   // .catch(this.handleErrorPromise);;
                   //return true;
    }



update(obj:product):Observable<product[]> {
          let bodyString = JSON.stringify(obj); // Stringify payload
        console.log(bodyString);

  let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    return this.http.put(this.dataUrl+ '/update', {'data':obj}, options)
     .map((res:Response) => res.json());
           //.then(this.extractData)
           //.catch(this.handleErrorPromise);
    }

delete(obj:product):Observable<product[]> {
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    return this.http.delete(this.dataUrl+ '/Delete/'+obj._id, options)
     .map((res:Response) => res.json());
    }


    private extractData(res: Response) {
      let body = res.json();
      console.log(body);
      return body || [];
    }
    private handleErrorObservable (error: Response | any) {
	       console.error(error.message || error);
	        return Observable.throw(error.message || error);
    }

    private handleErrorPromise (error: Response | any) {
	       console.error(error.message || error);
	        return Promise.reject(error.message || error);
    }

    saveProduct2(obj:FormData):Observable<product[]> {
        let headers = new Headers();
        
        var token = localStorage.getItem("token");
        headers.append("Authorization", "Bearer " + token);

        return this.http.post(this.dataUrl+ '/Addproduct2',obj, new RequestOptions({headers:headers}))
                          .map((res:Response) => res.json())
                          .catch((err)=> {return err;});

        }

        saveProduct3(obj:FormData):Observable<product[]> {
            let headers = new Headers();
            var token = localStorage.getItem("token");
            headers.append("Authorization", "Bearer " + token);
    
            return this.http.post(this.dataUrl+ '/Addproduct3',obj, new RequestOptions({headers:headers}))
                              .map((res:Response) => res.json())
                              .catch((err)=> {return err;});
    
            }
}



