import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';
import { FormsModule,ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { productService } from './services/product.service';
import { countryService } from './services/country.service';
import { userService } from './services/user.service';
import { AppComponent } from './app.component';
import { AppRoutingModule,routingComponents } from './app.routing';
import { ToastrModule } from 'ngx-toastr';
import { CanActivateAuthGuard } from './guard/auth.guards';
import { logService } from './services/log.service';



@NgModule({
  declarations: [
    AppComponent,
    routingComponents,
    ],
  imports: [
    BrowserModule,BrowserAnimationsModule,AppRoutingModule,FormsModule,ReactiveFormsModule,HttpModule,
    ToastrModule.forRoot()
  ],

providers: [productService,userService,CanActivateAuthGuard,countryService,logService],
  bootstrap: [AppComponent]
})
export class AppModule { }



