import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import {product} from './../models/product';
import {productService} from './../services/product.service';
import { FormControl, FormBuilder, FormGroup, Validators, AbstractControl } from '@angular/forms';
import {ToastrService} from 'ngx-toastr';

@Component({
  selector: 'app-product3',
  templateUrl: './product3.component.html',
  styleUrls: ['./product3.component.css']
})
export class Product3Component implements OnInit {
  products:product[]=[];
  product:product;
  loading: boolean = false;
  _file = new File([""], "");

  @ViewChild('fileInput') fileInput: ElementRef;

  dbForm:FormGroup=this.builder.group({//^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$
    name:new FormControl(null),
    st_date:new FormControl(null),
    description:new FormControl(null),
    price:new FormControl(null),
    avatar: null
  });
  constructor(private builder:FormBuilder,private dataservice:productService,private toaster:ToastrService) { 
    this.product = new product();
  }

  ngOnInit() {
    this.getdata();
  }
  getdata(){
    this.dataservice.get().subscribe(data=>{this.products=data;
    console.log(data);
    this.dbForm.reset();
    this.clearFile();
    });
  }
  save(){
    
    console.log(this.dbForm.value);

    const formData: any = new FormData();
    //formData.append("uploads[]", this._file, this._file.name);
    formData.append("uploads[]", this._file, this._file.name);
    formData.append("name",this.dbForm.value.name);
    formData.append("st_date",this.dbForm.value.st_date);
    formData.append("description",this.dbForm.value.description);
    formData.append("price",this.dbForm.value.price);
  
    // var token = localStorage.getItem("token");
    // console.log(token);
    // formData.append('token',"Bearer " + token);
    this.dataservice.saveProduct3(formData).subscribe(data=>this.getdata(),
    error=>{
      if(error.status == 401){
        this.toaster.error(error.statusText,'Operation failed');
      }
      
      // console.log('Error');
      // console.log(error.status);
      // console.log(error);
    });
  }
  fileChange(event) {
    let fileList: FileList = event.target.files;
    this._file = event.target.files[0];
  }
  
clearFile() {
    this.dbForm.get('avatar').setValue(null);
    this.fileInput.nativeElement.value = '';
  }

  delete(obj){
    if(confirm('Are you sure to delete?')){
      this.dataservice.delete(obj).subscribe(data=>this.getdata());
    }
  }
  edit(obj){
    this.dbForm.patchValue(obj);
  }
}


