import { Component, OnInit } from '@angular/core';
import { country,state } from './../models/country';
import { countryService } from './../services/country.service';
import { FormControl, FormBuilder, FormGroup, Validators, AbstractControl } from '@angular/forms';

@Component({
  selector: 'app-states',
  templateUrl: './states.component.html',
  styleUrls: ['./states.component.css']
})
export class StatesComponent implements OnInit {
  objArray:state[]=[];
  objCountryArray:country[]=[];
  loading: boolean = false;

  dbForm:FormGroup=this.builder.group({//^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$
    name:new FormControl(null),
    _id:new FormControl(null)
  });
  constructor(private dataservice:countryService,private builder:FormBuilder) { }
  ngOnInit() {
    this.getdata();
  }
  getdata(){
    this.dataservice.get().subscribe(data=>{
      this.objCountryArray=data;
    });
  }
  save(){
    if(this.dbForm.value._id == null) { //Save
      this.dataservice.save(this.dbForm.value).subscribe(data=>{
        console.log(data);
        this.getdata();
      });
    }
    else { //update
      this.dataservice.update(this.dbForm.value).subscribe(data=>{
        console.log(data);
        this.getdata();
      });
    }
  }
  delete(id){
      if(confirm('Are You Sure To Delete! Record Permanently')){
        this.dataservice.delete(id).subscribe(data=>{
          console.log(data);
          this.getdata();
        });
      }
  }
  edit(obj){
    this.dbForm.patchValue(obj);
  }
  cancel(){
    this.dbForm.reset();
  }

}



