
export class LogActivity{
    activityname:string;
    ipaddress:string;
    adminread:boolean;
    userread:boolean;
    comment:string;
    user_id:string;
}
import * as Appconfig from "../services/common";
import { Injectable,ChangeDetectorRef } from '@angular/core';


import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Observable } from 'rxjs';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/toPromise';

@Injectable()
export class logService {

  private dataUrl = Appconfig.data.Url+'log';//'http://localhost:3001/log';  // URL to web api
      testResponse: any;

  constructor(private http:Http) {}

get(): Observable<any[]> {
     return this.http.get(this.dataUrl+ '/list')
     .map((res:Response) => res.json());

 }


save(obj:LogActivity):Observable<any[]> {
    console.log('SaveData - 2');
    let bodyString = JSON.stringify(obj); // Stringify payload
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    return this.http.post(this.dataUrl+ '/save',obj,options)
                .map((res:Response) => res.json());
    }




}



