import { NgModule } from '@angular/core';
import { RouterModule, Routes,CanActivate } from "@angular/router";

import { HomeComponent } from './home/home.component';
import { ContactusComponent } from './contactus/contactus.component';
import { ProductComponent } from './product/product.component';
import { UserRegstrationComponent } from './user-regstration/user-regstration.component';
import { LoginComponent } from './login/login.component';

import { CanActivateAuthGuard } from './guard/auth.guards';

import { CountryComponent } from './country/country.component';
import { StatesComponent } from './states/states.component';
import { Product2Component } from './product2/product2.component';
import { Product3Component } from './product3/product3.component';

const APP_ROUTES = [ 
{ path: '', redirectTo: 'signin', pathMatch: 'full' }, 
{ path: 'home', component: HomeComponent },
{ path: 'contactus', component: ContactusComponent },
{ path: 'products', component: ProductComponent, canActivate:[CanActivateAuthGuard] },
{ path: 'product2', component: Product2Component },
{ path: 'product3', component: Product3Component },
{ path: 'register', component: UserRegstrationComponent },
{ path:'signin', component: LoginComponent },
{ path:'country', component: CountryComponent },
{ path:'states/:id', component: StatesComponent },
];


@NgModule({
    imports: [ RouterModule.forRoot(APP_ROUTES) ],
    exports: [ RouterModule ]
  })
  export class AppRoutingModule {}
   
  export const routingComponents = [
    HomeComponent,ContactusComponent,ProductComponent,UserRegstrationComponent,
    LoginComponent,CountryComponent,StatesComponent,Product2Component,Product3Component
  ];


