import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import {Router} from '@angular/router';
import {user} from './../models/user';
import {userService} from './../services/user.service';
import { FormControl, FormBuilder, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { doesNotThrow } from 'assert';
import { domRendererFactory3 } from '../../../node_modules/@angular/core/src/render3/renderer';
import {ToastrService} from 'ngx-toastr';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  _users:user[]=[];
  _user:user;
  loading: boolean = false;
public submitted:boolean;

  @ViewChild('fileInput') fileInput: ElementRef;

  public dbForm:FormGroup=this.builder.group({//^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$
    username:new FormControl(null,[Validators.required,Validators.minLength(3)]),
    password:new FormControl(null,[Validators.required,Validators.minLength(5)]),
   });
  constructor(private builder:FormBuilder,private dataservice:userService,
    private toaster:ToastrService,private routes:Router) { 
    this._user = new user();
  }

  ngOnInit() {
    //this.getdata();
  }
  getdata(){
    this.dataservice.get().subscribe(data=>{this._users=data;
    console.log(data);
    });
  }
  save(__user:user,isValid:boolean){
    this.submitted = isValid;
    console.log(__user);
    if(isValid){
      console.log(this.dbForm.value);
      this.dataservice.signin(__user).subscribe((data)=>{
        console.log(data);
        
        if(data.success == true){
          this.toaster.success('sucessfully!','Operation success1');
          localStorage.setItem('token',data.token);
          localStorage.setItem('username',data.username);
          console.log(data.token);
          this.routes.navigateByUrl("/products");
        }
        else{
          this.toaster.error(data.message,'Operation failed');
        }
      },
      err=>{
        this.toaster.error(err.message,'Operation failed');
      });
        //this.getdata(),
    
    }
  }


}



